.mplayer{
  display: flex;
  .btn {
    padding: 0 17px;
    background-color: #fff;
    color: #000;
    display: flex;
    align-items: center;
    margin: 5px 0 5px -7px;
    cursor: pointer;
    position: relative;
    .toggle {
      position: absolute;
      padding: 13px;
      left: 0;
      top: 20px;
      box-sizing: border-box;
    }
    .play {
      &::after {
        content: "||";
        width: 16px;
        height: 16px;
        line-height: 15px;
        font-size: 11px;
        text-align: center;
        border-radius: 8px;
        background-color: #666;
        color: #999;
        animation: plays 1.5s linear infinite;
        position: absolute;
        top: 4px;
        left:4px;
      }
    }
    .pause{
      &::after {
        content: "";
        position: absolute;
        top: 5px;
        left: 7px;
        border: 13px solid rgba(0,0,0,0);
        border-top-width: 7px;
        border-bottom-width: 7px;
        border-left-color: #666;
      }
    }
  }
}

@keyframes plays {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(360deg);
  }
}