.App {
  height: 100%;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  .playerWrap{
    position: absolute;
    top:0;
    left:0;
    z-index: 1;
    transition: all 1s cubic-bezier(.5,-1,1,1);
  }
  .hideWrap{
    position: absolute;
    top:0;
    left:-310px;
    z-index: 1;
    transition: all 1s cubic-bezier(.5,-1,1,1);
  }
  .btns {
    position: absolute;
    top: 72%;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1;
    display: flex;
    flex-wrap: nowrap;
    white-space: nowrap;
  }
}
.App::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url(assets/images/bg.jpg) no-repeat;
  opacity: 1;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
}

@function fact($number) {
    $value: 1;
    @if $number>0 {
        @for $i from 1 through $number {
            $value: $value * $i;
        }
    }
    @return $value;
}

@function pow($number, $exp) {
    $value: 1;
    @if $exp>0 {
        @for $i from 1 through $exp {
            $value: $value * $number;
        }
    }
    @else if $exp < 0 {
        @for $i from 1 through -$exp {
            $value: $value / $number;
        }
    }
    @return $value;
}

@function rad($angle) {
    $unit: unit($angle);
    $unitless: $angle / ($angle * 0 + 1);
    @if $unit==deg {
        $unitless: $unitless / 180 * pi();
    }
    @return $unitless;
}

@function pi() {
    @return 3.14159265359;
}

@function sin($angle) {
    $sin: 0;
    $angle: rad($angle);
    // Iterate a bunch of times.
    @for $i from 0 through 10 {
        $sin: $sin + pow(-1, $i) * pow($angle, (2 * $i + 1)) / fact(2 * $i + 1);
    }
    @return $sin;
}

@function cos($angle) {
    $cos: 0;
    $angle: rad($angle);
    // Iterate a bunch of times.
    @for $i from 0 through 10 {
        $cos: $cos + pow(-1, $i) * pow($angle, 2 * $i) / fact(2 * $i);
    }
    @return $cos;
}

@function tan($angle) {
    @return sin($angle) / cos($angle);
}

@function spring-wobbly($t) { 
  @return -0.5 * pow(2.71828, (-6 * $t)) * (-2 * pow(2.71828, (6 * $t)) + sin(12 * $t) + 2 * cos(12 * $t));
}

@function lerp($a, $b, $p) {
  @return $a + $p * ($b - $a);
}

@keyframes move { 
  @for $i from 0 through 100
  { 
    #{$i}% { 
      left: lerp(100px, 200px, spring-wobbly($i / 100)); 
    } 
  } 
}


